
.navbar {
  padding: 10px;
}

.categories-list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.categories-list li {
  background-color: #ededed;
  margin-right: 10px;
  padding: 10px;
  font-weight: bold;
  font-size:17px ;
  cursor: pointer;
}

.categories-list li.active {
  background-color: #c8c8c8;
 
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  justify-items: center;
}

.product-item {
  text-align: center;
  padding: 10px;
  border-radius: 4px;
  width: 400px;
  max-width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;

}
.product-item p {
 display: none;
}


.product-item img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}


.product-item img {
  max-height: 300px;
 .products-grid {
  display: grid;
  grid-gap: 10px;
  justify-items: center;
}
}

.product-item h2 {
  margin-top: 0;
  font-size: 15px;
  font-weight: normal;
  white-space: nowrap;
  overflow: hidden;
  
}

 